import React, { Suspense, useState, useEffect, useCallback } from "react";

import "./App.css";

import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";

import ScrollTop from "./utils/scrollTop.js";

import NavBarArea from "./layout-components/NavBarArea/NavBarArea.js";
import FooterArea from "./layout-components/FooterArea/FooterArea.js";
import Loading from "./components/Loading/Loading.js";
import ErrorBoundary from "./components/ErrorBoundary/ErrorBoundary.js";
import Modal from "./components/Modal/Modal.js";
import Versions from "./components/Versions/Versions.js";
import VersionData from "./components/Versions/VersionData.js";


const HomePage = React.lazy(() => import("./pages/HomePage/HomePage.js"));
const DownloadPage = React.lazy(() => import("./pages/DownloadPage/DownloadPage.js"));
const HelpPage = React.lazy(() => import("./pages/HelpPage/HelpPage.js"));
const DonatePage = React.lazy(() => import("./pages/DonatePage/DonatePage.js"));
const ReportPage = React.lazy(() => import("./pages/ReportPage/ReportPage.js"));


const App = () => {
  const [languageSelected, setLanguageSelected] = useState(false);
  const [showLanguageSettings, setShowLanguageSettings] = useState(false);
  const [language, setLanguage] = useState("en");
  const [popupVersion, setPopupVersion] = useState(null);

  const changeLanguage = useCallback((language) => {
    setLanguage(language);
    const checkUrl = new URL(window.location.href);
    const defaultLang = "en";
    if (language === defaultLang) {
      checkUrl.searchParams.delete("lang");
    }
    else {
      checkUrl.searchParams.set("lang", language);
    }
    window.history.replaceState(null, null, checkUrl.href);
  }, []);

  useEffect(() => {
    const checkUrl = new URL(window.location.href);
    const lang = (checkUrl.searchParams.get("lang") || "").toLowerCase();
    if (["en", "ko"].includes(lang)) {
      changeLanguage(lang);
    }
    else {
      checkUrl.searchParams.delete("lang");
      window.history.replaceState(null, null, checkUrl.href);
    }
    const popup = checkUrl.searchParams.get("popup");
    if (VersionData.versionList().includes(popup)) {
      setPopupVersion(popup);
    }
    else {
      checkUrl.searchParams.delete("popup");
      window.history.replaceState(null, null, checkUrl.href);
    }
  }, [changeLanguage]);

  useEffect(() => {
    if (!languageSelected) {
      if (language === "ko" && !["ko", "ko-KR"].includes(navigator.language)) {
        setShowLanguageSettings(true);
        setLanguageSelected(true);
      }
    }
  }, [languageSelected, language]);

  const T = useCallback((en, ko) => {
    if (language === "en") {
      return en;
    }
    else {
      return ko;
    }
  }, [language]);

  if (popupVersion !== null)
    return (<div style={{margin: "-30px 0"}}><Versions version={popupVersion} showMeta={false} language={language} /></div>);
  return (
    <div className="App">
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <ScrollTop/>
        <NavBarArea language={language} languageChange={changeLanguage}/>
        <ErrorBoundary language={language}>
          <Suspense fallback={
            <Loading>
              {T("Loading...", "불러오는 중...")}
            </Loading>
          }>
            <Modal
              showModal={showLanguageSettings}
              modalTitle={"Language Settings"}
              okCallback={() => {
                changeLanguage("en");
                setShowLanguageSettings(false);
              }}
              okButton={"Switch to English"}
              cancelCallback={() => {
                setShowLanguageSettings(false);
              }}
              cancelButton={"Stay Here"}
            >
              You are currently on the Korean page.
              <br/>
              Would you like to switch to the English version?
            </Modal>
            <Switch>
              <Route exact path="/" render={() => <HomePage language={language} />} />
              <Route exact path="/download" render={() => <DownloadPage language={language} />} />
              <Route exact path="/help" render={() => <HelpPage language={language} />} />
              <Route exact path="/donate" render={() => <DonatePage language={language} />} />
              <Route exact path="/report" render={() => <ReportPage language={language} />} />
              <Redirect to="/" />
            </Switch>
          </Suspense>
        </ErrorBoundary>
        <FooterArea language={language}/>
      </BrowserRouter>
    </div>
  );
};

export default App;