import React, { useCallback } from "react";
import Nav from "react-bootstrap/Nav";
import NavBar from "react-bootstrap/Navbar";
import NavDropdown from "react-bootstrap/NavDropdown";
import "bootstrap/dist/css/bootstrap.min.css";

import "./NavBarArea.css";

import { Link, withRouter } from "react-router-dom";


const NavBarArea = ({language, languageChange}) => {
    const T = useCallback((en, ko) => {
        if (language === "en") {
            return en;
        }
        else {
            return ko;
        }
    }, [language]);

    const getPath = useCallback((path) => {
        const newUrl = new URL(window.location.href);
        newUrl.pathname = path;
        newUrl.searchParams.delete("download");
        newUrl.searchParams.delete("type");
        return newUrl.pathname + newUrl.search;
    }, []);

    const getCleanUrl = useCallback((path) => {
        const checkUrl = new URL(window.location.href);
        const lang = checkUrl.searchParams.get("lang");
        checkUrl.pathname = path;
        const params = new URLSearchParams();
        const defaultLang = "en";
        if (["en", "ko"].includes(lang)) {
            if (lang !== defaultLang) {
                params.set("lang", lang);
            }
        }
        checkUrl.search = params.toString();
        return checkUrl.href;
    }, []);

    const loadPage = useCallback((path) => {
        window.location.href = getCleanUrl(path);
    }, [getCleanUrl]);

    return (
        <div className="NavBar">
            <div className="nav-bar">
                <NavBar collapseOnSelect fixed="top" bg="light" expand="lg">
                    <Link className="navbar-brand" onClick={() => loadPage("/")}>
                        <svg style={{marginRight: "10px", width: "32px", height: "32px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1024 1024" fill="#FFFFFF">
                            <g>
                                <path d="M981 0 L981 338 L512 655 L43 338 L43 0 L512 317 Z"/>
                                <rect x="0" y="755" width="1024" height="269"/>
                            </g>
                        </svg>
                        TwitchLink
                    </Link>
                    <NavBar.Toggle/>
                    <NavBar.Collapse id="basic-navbar-nav">
                        <Nav className="mr-auto" activeKey={window.location.pathname}>
                            <Nav.Item>
                                <Nav.Link eventKey="/" as={Link} to={getPath("/")}>
                                    TwitchLink
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link eventKey="/download/" as={Link} to={getPath("/download/")}>
                                    {T("Releases", "버전 목록")}
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link eventKey="/help/" as={Link} to={getPath("/help/")}>
                                    {T("Manual & Help", "사용법 & 도움말")}
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link eventKey="/support/" href="https://github.com/devhotteok/TwitchLink" target="_blank">
                                    {T("Support & Issues", "지원 & 버그 제보")}
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link eventKey="/donate/" as={Link} to={getPath("/donate/")}>
                                    {T("Donate", "후원")}
                                </Nav.Link>
                            </Nav.Item>
                        </Nav>
                        <Nav>
                            <NavDropdown alignRight title={
                                <div style={{display: "flex", justifyContent: "center", alignItems: "center", gap: "10px"}}>
                                    <svg style={{width: "24px", height: "24px", color: "#000000"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
                                        <circle cx="12" cy="12" r="10"/>
                                        <path d="M12 2a14.5 14.5 0 0 0 0 20 14.5 14.5 0 0 0 0-20"/>
                                        <path d="M2 12h20"/>
                                    </svg>
                                    Language / 언어
                                </div>
                            }>
                                <NavDropdown.Item onClick={() => languageChange("en")} active={language === "en"}>ENGLISH</NavDropdown.Item>
                                <NavDropdown.Item onClick={() => languageChange("ko")} active={language === "ko"}>한국어</NavDropdown.Item>
                            </NavDropdown>
                        </Nav>
                    </NavBar.Collapse>
                </NavBar>
                <div className="nav-bar-area"></div>
            </div>
        </div>
    );
};

export default withRouter(NavBarArea);