import React, { useCallback } from "react";

import { Link } from "react-router-dom";

import './Versions.css';

import Content from '../../components/Contents/index.js';
import Button from '../../components/Buttons/index.js';
import Label from '../../components/Labels/Label/Label.js';

import VersionData from './VersionData.js';

const Version = ({version, showMeta, language}) => {
  const T = useCallback((en, ko) => {
    if (language === "en") {
      return en;
    }
    else {
      return ko;
    }
  }, [language]);

  const copyLink = (url) => {
    navigator.clipboard.writeText(url);
    alert(T("Copied to clipboard", "클립보드에 복사되었습니다."));
  };

  const CurrentVersionData = VersionData.data[version];
  const VersionReleased = CurrentVersionData["status"]["released"];
  const VersionNotSupported = !CurrentVersionData["status"]["supported"];
  const OperatingSystems = CurrentVersionData["os"];
  const UpdateFile = CurrentVersionData["updateNote"][language];
  const UpdateTitle = UpdateFile.UpdateTitle;
  const UpdateNote = UpdateFile.UpdateNote;
  const SourceUrl = UpdateFile.SourceUrl;
  return (
    <Content.Area className="Versions">
      <Content.Box>
        <Content.Subtitle>
          <UpdateTitle />
        </Content.Subtitle>
        <Content.TextArea>
          <UpdateNote />
        </Content.TextArea>
      </Content.Box>
      {
        showMeta ? (VersionReleased ?
            <div>
              <Content.SrcViewer>
                {SourceUrl}
              </Content.SrcViewer>
              <Button.ButtonArea>
                <Button.Button style={{display: "flex", padding: "8px 15px", minHeight: "48px", justifyContent: "center", alignItems: "center", gap: "6px"}} onClick={() => copyLink(SourceUrl)}>
                  <svg style={{width: "24px", height: "24px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
                    <rect width="14" height="14" x="8" y="8" rx="2" ry="2"/>
                    <path d="M4 16c-1.1 0-2-.9-2-2V4c0-1.1.9-2 2-2h10c1.1 0 2 .9 2 2"/>
                  </svg>
                  {T("Copy URL", "URL 복사")}
                </Button.Button>
                <Button.LinkButton style={{display: "flex", padding: "8px 15px", minHeight: "48px", justifyContent: "center", alignItems: "center", gap: "6px"}} href={SourceUrl} target="_blank">
                  <svg style={{width: "24px", height: "24px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
                    <path d="M21 13v6a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h6"/>
                    <path d="m21 3-9 9"/>
                    <path d="M15 3h6v6"/>
                  </svg>
                  {T("Source Code", "소스코드")}
                </Button.LinkButton>
                {
                  Object.entries(OperatingSystems).map(([key, value]) => {
                    if (!value) {
                      return null;
                    }
                    const displayName = {windows: "Windows", macos: "macOS"}[key];
                    return (
                      <Link className="Button" style={{
                        display: "flex",
                        padding: "8px 15px",
                        minHeight: "48px",
                        color: "#000000",
                        textDecoration: "none",
                        justifyContent: "center",
                        alignItems: "center",
                        gap: "6px"
                      }} to={`/donate?download=${version}&type=${key}`}>
                        {
                          key === "windows"
                          ?
                          <svg style={{width: "32px", height: "32px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" fill="currentColor">
                            <path d="M31.87 30.58H244.7v212.81H31.87zM266.89 30.58H479.7v212.81H266.89zM31.87 265.61H244.7v212.8H31.87zM266.89 265.61H479.7v212.8H266.89z"/>
                          </svg>
                          :
                          <svg style={{width: "32px", height: "32px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor">
                            <path d="M12.152 6.896c-0.948 0-2.415-1.078-3.96-1.04-2.04 0.027-3.91 1.183-4.961 3.014-2.117 3.675-0.546 9.103 1.519 12.09 1.013 1.454 2.208 3.09 3.792 3.039 1.52-0.065 2.09-0.987 3.935-0.987 1.831 0 2.35 0.987 3.96 0.948 1.637-0.026 2.676-1.48 3.676-2.948 1.156-1.688 1.636-3.325 1.662-3.415-0.039-0.013-3.182-1.221-3.22-4.857-0.026-3.04 2.48-4.494 2.597-4.559-1.429-2.09-3.623-2.324-4.39-2.376-2-0.156-3.675 1.09-4.61 1.091zM15.53 3.83c0.843-1.012 1.4-2.427 1.245-3.83-1.207 0.052-2.662 0.805-3.532 1.818-0.78 0.896-1.454 2.338-1.273 3.714 1.338 0.104 2.715-0.688 3.559-1.701"></path>
                          </svg>
                        }
                        {T(`Download for ${displayName} (Version: ${version})`, `${displayName}용 다운로드 (버전: ${version})`)}
                        {
                          version === VersionData.latestVersion()
                          ?
                          T(" (Latest)", " (최신 버전)")
                          :
                          null
                        }
                      </Link>
                    );
                  })
                }
              </Button.ButtonArea>
              {VersionNotSupported &&
                <div style={{display: "flex", padding: "5px 15px", gap: "2px"}}>
                  <svg style={{width: "24px", height: "24px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
                    <circle cx="12" cy="12" r="10"></circle>
                    <line x1="12" y1="16" x2="12" y2="12"></line>
                    <line x1="12" y1="8" x2="12.01" y2="8"></line>
                  </svg>
                  <Label>
                    {T("This version is no longer supported and is not compatible.", "이 버전의 지원이 종료되었으며 호환되지 않습니다.")}
                  </Label>
                </div>
              }
            </div>
          :
            <div>
              <div style={{display: "flex", padding: "5px 15px", gap: "2px"}}>
                <svg style={{width: "24px", height: "24px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
                  <circle cx="12" cy="12" r="10"></circle>
                  <line x1="12" y1="16" x2="12" y2="12"></line>
                  <line x1="12" y1="8" x2="12.01" y2="8"></line>
                </svg>
                <Label>
                  {T("This version has not been released yet.", "이 버전은 아직 공개되지 않았습니다.")}
                </Label>
              </div>
              <Link className="Button supportButton" to={"/donate"}>{T("Support Development", "개발에 기여하기")}</Link>
            </div>
          )
        :
          (
            VersionReleased ?
              null
            :
              <div style={{display: "flex", padding: "5px 15px", gap: "2px"}}>
                <svg style={{width: "24px", height: "24px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
                  <circle cx="12" cy="12" r="10"></circle>
                  <line x1="12" y1="16" x2="12" y2="12"></line>
                  <line x1="12" y1="8" x2="12.01" y2="8"></line>
                </svg>
                <Label>
                  {T("This version has not been released yet.", "이 버전은 아직 공개되지 않았습니다.")}
                </Label>
              </div>
          )
      }
    </Content.Area>
  );
};

export default Version;